
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: firebrick;
  margin-top: 60px;
}

.hello {
  background: -webkit-gradient(linear, left bottom, left top, from(papayawhip), color-stop(ivory), to(white));
  background: linear-gradient(to top, papayawhip, ivory, white);
  height: 100%;
}
h1, h2 {
  font-weight: normal;
  color: black;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 6px;
}
.selected {
  font-style: italic;
}
.buttonized {
  padding: 6px;
  border: 2px;
  border-radius: 1rem;
  background-color: darkgreen;
  color: ivory;
  font-weight: bold;
}
a {
  color: darkgreen;
}
.card {
  background: -webkit-gradient(linear, left bottom, left top, from(gainsboro), to(snow));
  background: linear-gradient(to top, gainsboro, snow);
}
